.film {
  width: 400px;
  padding: 60px;
  color: #fff;
  position: relative;
}

.overview {
  text-align: justify;
  margin-bottom: 20px;
  overflow: hidden;
}

.buttonWrapper {
  display: flex;
  justify-content: space-around;
  margin-bottom: 30px;
}

.showMoreButton {
  cursor: pointer;
  background: transparent;
  height: 40px;
  width: 100%;
  outline: none;
  font-size: 18px;
  font-weight: 300;
  border-radius: 15px;
  border: 3px solid #1e8a71;
}

.showMoreButton:hover {
  background: #2b3c46;
}

.showMoreButton:hover .showMoreLink {
  color: #fff;
}

.showMoreLink {
  color: #1e8a71;
  line-height: 35px;
  text-decoration: none;
  height: 40px;
  display: block;
  width: 100%;
}

.title {
  margin-top: 20px;
  margin-bottom: 20px;
}

.vote {
  color: #e1e4d9;
  background: #cf7a05;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  line-height: 50px;
  font-weight: 900;
  box-shadow: 0px 0px 10px 5px rgb(40, 53, 52);
  position: absolute;
  top: 220px;
  right: 70px;
}

.button {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.watchedButton {
  fill: #1e8a71;
}

.deleteWatchedButton {
  fill: #cf7a05;
  transform: rotate(-360deg);
}

.favButton {
  fill: #1e8a71;
}

.deleteFavButton {
  fill: #cf7a05;
  transform: rotate(-360deg);
}

.favButton,
.deleteFavButton {
  height: 50px;
  width: 50px;
}

.favButton,
.deleteFavButton,
.deleteWatchedButton,
.watchedButton {
  padding: 3px;
  outline: none;
  transition: 1s ease-in;
}

.deleteWatchedButton,
.watchedButton {
  height: 53px;
  width: 53px;
}

.favButton:hover,
.deleteFavButton:hover,
.deleteWatchedButton:hover,
.watchedButton:hover {
  padding: 0;
}

.img {
  width: 200px;
  height: 300px;
}
