.arrow,
.doubleArrow {
  fill: #fff;
}

.firstArrow,
.backArrow {
  transform: rotate(180deg);
  fill: #fff;
}

.button,
.page {
  background: transparent;
  border-radius: 60px;
  padding: 20px;
  outline: none;
  width: 60px;
  margin: 15px;
  height: 60px;
  border: 2px solid #fff;
}

.button {
  cursor: pointer;
}

.button:disabled,
.button[disabled] {
  cursor: auto;
}

.pagination {
  width: 100%;
  display: flex;
  padding: 30px;
  justify-content: center;
}

.page {
  font-size: 18px;
  color: #fff;
  padding: 19px;
  text-align: center;
  padding: 0;
  vertical-align: bottom;
  line-height: 57px;
}

.button:disabled,
.button[disabled] {
  background-color: rgba(85, 112, 108, 0.788);
  border: 2px solid rgba(255, 255, 255, 0.253);
  opacity: 0.5;
}
