.title {
  color: #fff;
  text-align: left;
  margin-top: 50px;
  margin-left: 130px;
  font-weight: 300;
  font-size: 26px;
}

.form {
  color: #000;
  position: relative;
  width: 1000px;
  margin: 0 auto;
  margin-top: 80px;
  margin-bottom: 100px;
}

.input {
  width: 1000px;
  height: 42px;
  font-weight: 500;
  font-size: 18px;
  outline: none;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  border-radius: 20px;
  box-shadow: 10px 5px 11px 3px rgba(19, 18, 18, 0.199);
}

.button {
  position: absolute;
  top: 2px;
  cursor: pointer;
  right: 2px;
  width: 38px;
  height: 38px;
  outline: none;
  border: none;
  color: #fff;
  background: #249f81;
  box-shadow: 0px 0px 20px 0px rgba(19, 18, 18, 0.555);
  border-radius: 38px;
}

.button:hover {
  background: #1e2931de;
}

main {
  position: relative;
}

.autosuggest {
  border-radius: 20px;
  background-color: #fff;
  top: 220px;
  position: absolute;
  z-index: 100000;
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

.autosuggestList {
  list-style: none;
}

.autosuggestTitle {
  background-color: #249f81;
  color: #fff;
  font-weight: 900;
  padding: 5px;
  font-size: 20px;
  width: 1000px;
}

.autosuggestButton {
  width: 1000px;
  outline: none;
  border: none;
  background: transparent;
  height: 25px;
  font-size: 15px;
  padding: 5px;
  margin: 0 auto;
  cursor: pointer;
}

.autosuggestButton:hover {
  color: #249f81;
}
